header{
    background: linear-gradient(to top, #002147 0%, #537895 100%);;
    height: 90vh;
}

header .container{
    height: 50vh;
    width: 80vw;
    margin: 0 10vw;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.left{
    width: 30vw;
}
.left h1{
    font-style: bold;
    font-size: 45px;
    line-height: 0.96;
}
.left p{
    font-style: light;
    font-size: 22px;
    color: #ffffffc1;
}
.left button{
    padding: 10px 20px;
    border-radius: 20px;
    cursor: pointer;
    margin-top: 1vw;
    background-color: #fff;
    color: #002147;
    font-size: 16px;
    transition: ease-in-out 0.3s;
}
.left button:hover{
    color:#fff;
    border-color: #fff;
    background-color: #00214700;
    transition: ease-in-out 0.3s;
}



.right img{
    margin-top: 5vw;
    animation: fadeInUp 0.8s ease forwards, float 3s ease-in-out infinite;
    opacity: 0; 
    animation-delay: 0.2s;
}


.left h1, .left p, .left button {
    animation: fadeInUp 0.8s ease forwards; 
    opacity: 0; 
}

.left h1 {
    animation-delay: 0.2s;
}
.left p {
    animation-delay: 0.4s; 
}
.left button {
    animation-delay: 0.6s; 
}




@keyframes float {
    0%, 100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-20px);
    }
}
@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 768px) {
    header {
        height: auto; 
        padding: 5vw 0 10vw 0;
    }

    header .container {
        height: auto;
        width: 90vw;
        margin: 0 5vw;
        flex-direction: column; 
        text-align: center;
    }

    .left {
        width: 100%; 
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .left h1 {
        font-size: 34px;
        line-height: 1.2;
    }

    .left p {
        width: 80%;
        font-size: 19px;
        margin: 10px 0; 
    }

    .left button {
        margin-top: 15px;
    }

    .right, .right img {
        display: none;
    }
}