#contacts{
    background-color: #eef4fb;
    display: flex;
    justify-content: space-around;
    text-align: center;
}
.contacts-container {
    padding: 2vw;
    max-width: 30vw;
    text-align: center;
}

.contacts-container h2 {
    color: #002147;
    font-size: 24px;
    margin-bottom: 1vw;
}

.contacts-info {
    font-size: 18px;
    color: #555;
    margin-bottom: 1.5vw;
}

.contact-details {
    margin: 1vw 0;
}

.contact-details p {
    font-size: 16px;
    color: #333;
}

.contact-details p strong {
    color: #002147;
}

.social-media {
    margin-top: 1.5vw;
}

.social-media p {
    font-size: 18px;
    color: #555;
}

.social-media a {
    display: inline-block;
    color: #002147;
    margin: 0 0.5vw;
    text-decoration: none;
    font-size: 16px;
    transition: color 0.3s;
}

.social-media a:hover {
    color: #003e85;
}

@media (max-width: 768px) {
    .contacts-container {
        max-width: 80vw;
    }
}