* {
  font-family: inherit;
  color: #fff;
}

.menu {
  font-size: 16px;
  margin-left: 10vw;
  display: flex;
  padding: 2vw 4vw;
  align-items: center;
  justify-content: space-around;
  position: relative;
  transition: all 0.3s ease;
}

.menu ul {
  width: 55vw;
  margin-left: 10vw;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

#logo {
  width: 5vw;
}

.menu li {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  transition: ease-in-out 0.3s;
}

.menu li:hover {
  cursor: pointer;
  color: #002147;
  transition: ease-in-out 0.3s;
}

.menu button {
  padding: 10px 20px;
  border-radius: 20px;
  background-color: #00214700;
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
  transition: ease-in-out 0.3s;
}

.menu button:hover {
  color: #002147;
  border: 1px solid #002147;
  font-size: 14px;
  transition: ease-in-out 0.3s;

}

.active {
  color: #002147;
}

.menu.sticked {
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  padding: 0.5vw 1vw;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  margin: 0;
}

.menu.sticked li,
.menu.sticked button {
  color: #002147;
}

.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  width: 30px;
  height: 20px;
  cursor: pointer;
  z-index: 2000;
  transition: opacity 0.3s ease, background-color 0.3s ease;
}

.burger span {
  display: block;
  height: 3px;
  margin: 1vw 0;
  width: 100%;
  background-color: #fff;
  transition: all 0.3s ease;
}

.menu.sticked .burger {
  display: flex;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 3000;
}

.menu.sticked .burger span {
  background-color: #002147;
}

@media (max-width: 768px) {
  .burger {
    display: flex;
  }

  .menu {
    flex-direction: column;
    align-items: flex-end;
  }

  .menu ul, .menu.sticked ul, #logo, .menu.sticked.hidden {
    display: none;
  }


  .menu.sticked {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    background-color: #fff;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}

.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1500;
  display: flex;
  justify-content: flex-end;
}

.mobile-menu {
  background-color: #002147;
  width: 75%;
  max-width: 300px;
  height: 100%;
  padding: 20px;
  box-shadow: -4px 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.mobile-menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.mobile-menu li {
  margin-bottom: 20px;
  font-size: 18px;
  cursor: pointer;
  color: #fff;
  transition: color 0.3s ease;
}

.mobile-menu li:hover {
  color: #537895;
}

.mobile-menu button {
  padding: 10px 20px;
  border-radius: 20px;
  background-color: #fff;
  color: #002147;
  cursor: pointer;
  border: none;
  width: 100%;
}

.mobile-menu button:hover {
  background-color: #537895;
  color: #fff;
}