#whyUs {
    padding: 10vh 0 5vh 0;
    margin-top: 10vh;
    background-color: #eef4fb;
    text-align: center;
}

#services {
    padding: 10vh 0 5vh 0;
    margin-top: 5vh;
    background-color: #ffffff;
    text-align: center;
}

#whyUs p,
#services p {
    color: black;
}

#whyUs .subArticle,
#services .subArticle {
    color: #002147;
    font-size: 19px;
}

#whyUs .article,
#services .article {
    color: black;
    font-style: thin;
    font-size: 30px;
}

#whyUs .container {
    text-align: center;
    margin-top: 5vw;
}

#whyUs ul {
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 2vw;
}

#whyUs ul li {
    width: 20vw;
}

#whyUs ul h3,
.serviceBlock h3 {
    font-weight: 350;
    font-size: 25px;
    margin: 0;
}

#whyUs ul p,
.serviceBlock p {
    font-weight: 300;
    font-size: 18px;
    color: rgb(31, 31, 31);
}


#services .container {
    margin: 5vw 20vw 0 20vw;
    display: flex;
    align-items: center;
}

.serviceBlock {
    width: 50vw;
    display: flex;
    margin: 0 2vw;
    overflow: hidden;
}

.serviceBlock img {
    padding-top: 1vw;
    width: 20vw;
    height: 20vw;
    object-fit: cover;
    object-position: center;
}

.serviceBlock div {
    padding: 0 2vw;
}

#services button {
    background-color: transparent;
    border: none;
    color: #002147;
    font-size: large;
    transition: ease-in-out 0.3s;
}

#services button:hover {
    cursor: pointer;
    font-size: x-large;
    transition: ease-in-out 0.3s;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.fade-in-up {
    animation: fadeInUp 0.6s forwards;
}



@keyframes slide-in-left {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slide-in-right {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slide-out-left {
    from {
        transform: translateX(0);
        opacity: 1;
    }
    to {
        transform: translateX(-100%);
        opacity: 0;
    }
}

@keyframes slide-out-right {
    from {
        transform: translateX(0);
        opacity: 1;
    }
    to {
        transform: translateX(100%);
        opacity: 0;
    }
}

.slide-in-left {
    animation: slide-in-left 0.6s forwards;
}

.slide-in-right {
    animation: slide-in-right 0.6s forwards;
}

.slide-out-left {
    animation: slide-out-left 0.6s forwards;
}

.slide-out-right {
    animation: slide-out-right 0.6s forwards;
}



@media (max-width: 768px) {
    #whyUs ul {
        flex-direction: column;
        align-items: center;
        width: 80vw;
    }
    
    #whyUs ul li {
        width: 100%;
    }
    #whyUs .serviceBlock div {
        display: flex;
        flex-direction: column;
    }
}