#request {
    padding: 2vw;
    margin: auto;
    background-color: #ffffff;
    display: flex;
}

.request-form {
    width: 50vw;
    display: flex;
    flex-direction: column;
}
.request-form p{
    color: #002147;
    font-weight: 350;
    font-size: 25px;
}

.request-form label {
    margin-bottom: 1vw;
    font-weight: bold;
    color: #002147 !important;
}

.request-form select,
.request-form input,
.request-form textarea {
    padding: 8px;
    font-size: 16px;
    margin: 5px 0 0 1vw;
    color: #002147 !important;
}

.request-form textarea {
    width: 100%;
    padding-top: 1vw;
    margin: 0;
    resize: vertical;
    border: none; 
    border-bottom: 2px solid #002147;
    outline: none;
    font-family: inherit;
    box-sizing: border-box;
}


.request-form select {
    width: 9vw;
    font-size: 16px;
    padding: 8px;
    border: 1px solid #002147;
    outline: none;
    color: #002147;
    appearance: none; 
    font-weight: normal; 
    cursor: pointer;
}


.request-form select option {
    color: #002147; 
    font-weight: normal;
}

.request-form select option:checked {
    font-weight: bold; 
}

.request-form button {
    padding: 10px;
    font-size: 16px;
    color: #fff;
    background-color: #002147;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px;
    transition: ease-in-out 0.3s;
}


.request-form button:hover {
    background-color: #003e85;
    transition: ease-in-out 0.3s;
}

@media (max-width: 768px) {
    .request-form select {
        width: 20vw;
    }
}