#aboutUs{
    margin-top: 20vh;
    background-color: #fff;
    text-align: center;
}

#aboutUs p, h3, button{
    color: black;
}
#aboutUs .subArticle{
    color: #002147;
    font-size: 19px;
}
.article{
    color: black;
    font-style: thin;
    font-size: 30px;
}

#aboutUs .container {
    width: 80vw;
    margin: 10vh 10vw 0 10vw;
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
}
.container .left img{
    width: 40vw;
    height: 30vw;
    object-position: center;
    object-fit: cover;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

.right{
    width: 30vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start; 
    justify-content: start;
    padding-left: 8vw;
    vertical-align: top;
}
.right h3, .right p {
    text-align: left;
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}
.right h3{
    font-weight: 350;
    font-size: 25px;
    margin: 0;
}
.right p{
    font-weight: 300;
    font-size: 18px;
    color: rgb(31, 31, 31);
}
.right ul{
    width: 30vw;
    list-style-type: none;
    padding-left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.right button{
    padding: 7px 10px;
    border-radius: 20px;
    cursor: pointer;
    margin-top: 1vw;
    font-size: 16px;
    transition: ease-in-out 0.3s;
}
.right .active{
    background-color: #002147;
    color: #fff;
}.right .notActive{
    background-color: #fff;
    color: #002147;
}
.right button:hover{
    background-color: #002147;
    color: #fff;
    transition: ease-in-out 0.3s;
}



@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px); 
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.fade-in-up {
    animation: fadeInUp 0.6s forwards;
}


@media (max-width: 768px) {
    #aboutUs .container{
        flex-direction: column;
    }
    .right{
        width: 90%;
        padding-left: 5%;
    }
    .right h3, .right p {
        display: inline-block;
        width: 100%;
        text-align: center;
    }
    .right ul{
        width: 100%;
    }
}