#reviews {
    padding: 5vh 0 5vh 0;
    background-color: #eef4fb;
    text-align: center;
}

#reviews p,
h3,
button {
    color: black;
}

#reviews .subArticle {
    color: #002147;
    font-size: 19px;
}

#reviews .article {
    color: black;
    font-style: thin;
    font-size: 30px;
}

#reviews .container {
    display: flex;
    align-items: center;
    justify-content: center;
}

#reviews .review-slide {
    width: 300px;
    padding: 20px;
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 8px;
}

#reviews .review-photo {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

#reviews button {
    background-color: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
}


.reviews{
    display: flex;
    justify-content: space-evenly;
    width: 80vw;
    margin: 5vw 10vw 0 10vw;
}

.review{
    background-color: #ffffffe4;
    width: 17vw;
    box-shadow: 0 3px 5px rgb(226, 226, 226);
    padding: 0 1.5vw 1vw 2.5vw;
    margin-top: -10px;
    text-align: left;
    justify-content: space-between;
}
.review img{
    width: 7vw;
    height: 7vw;
    border-radius: 50%;
    object-fit: cover;
    object-position: center;
    position: relative;
    top: -2.5vw;
    left: -0.7vw;
}
.review .text{
    font-weight: 300;
    font-size: 16px;
    color: rgb(65,65, 65);
    flex-grow: 1;
}
.review .info{
    margin-top: auto;
    padding-top: 1vw;
}
.info .name{
    font-weight: 600;
    font-size: 18px;
    color: rgb(0, 0, 0);
}
.info .service{
    font-weight: 300;
    font-size: 17px;
    color: rgb(41, 41, 41);
}



#reviews .subArticle, #reviews .article, #reviews .review {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.6s ease, transform 0.6s ease;
}
#reviews .subArticle-visible {
    opacity: 1;
    transform: translateY(0);
}
#reviews .article-visible {
    opacity: 1;
    transform: translateY(0);
}
#reviews .review-visible {
    opacity: 1;
    transform: translateY(0);
}


@media (max-width: 768px) {
    .reviews{
        flex-direction: column;
        align-items: center;
    }
    #reviews .article{
        margin-bottom: 10vw;
    }
    .reviews div{
        width: 80%;
        margin-bottom: 15vw;
    }
    .reviews div p{
        margin: 0 5vw 2vw 5vw;
    }
    .review img{
        width: 15vw;
        height: 15vw;
        top: -5vw;
        left: 1vw;
    }
}